	module.exports = {"data":{"markdownRemark":{"html":"<p>今朝、私は「最後の誘惑」と呼ばれるアメリカの論争の的になる映画のビデオを見ました。映画が当時撮影された後、宗教界からの激しい議論と抗議が行われた。米国は非常に大きなパレードを持ち、禁止を要求した。主なプロットはキリストを記述することですが、ストーリーの発展は聖書に記載されているものとは異なります。主な違いは、キリストが十字架につけられた後、神は彼を養ったが、彼はもはや聖人ではなく、死人であり、結婚し、子どもを抱え、いくつかの女性に関連付けられていたということです。信仰によって決まるわけではありませんが、イエスを売却したと信じられているユダの意志は、より明確です。結局、イエスの信仰はしっかりとしておらず、神の御心を放棄したと非難しました。これはもちろん聖書とは異なりますが、イエスのための「ろう者」もあります。それはキリスト教のコミュニティから強い抗議を引き起こしました。これは別の神聖な物語を傷つけるために神聖な物語を使うことです。</p>\n<p>私は、私が見た食卓を思い出しました。これは、ジョーンズの宗教的事件に基づく物語です。何人かの人々が森に住んでおり、そのうちの1人が特別な種類の宗教を教えています。もちろん、そのような宗教は、現代の宗教とは異なり、より迷信であり、無知であり、人間の意志と理性を封じ込めるための物理的強制を含む、人間の魂に対する一種の制御です。人々はなぜ信仰を必要としているのですか？特に無知な教義のために、アメリカのダビデの宗派のように、なぜ人々は心から心がけていますか？これは、人々の心理的ニーズ、および人々の社会環境から分析することができる。この問題を信じることは非常に困惑しています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0109.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0109.md","frontmatter":{"title":"6月9日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0109.md absPath of file >>> MarkdownRemark"}}

